}

.header-container__burger span {
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #f5dd3c;
  border-radius: 2px;
  transition: 0.3s cubic-bezier(0.8, 0.5, 0.2, 1.4);
  transition-duration: 500ms;
}

.header-container__burger span:nth-child(1) {
  top: 0;
  left: 0;
}

.header-container__burger span:nth-child(2) {
  top: 13px;
  left: 0;
  opacity: 1;
}

.header-container__burger span:nth-child(3) {
  bottom: 0;
  left: 0;
}

.header-container__burger.open span:nth-child(1) {
  top: 13px;
  transform: rotate(45deg);
}

.header-container__burger.open span:nth-child(2) {
  opacity: 0;
}

.header-container__burger.open span:nth-child(3) {
  top: 13px;
  transform: rotate(-45deg);
}

.header-container__burger:not(.open):hover span:nth-child(1) {
  transform: rotate(-3deg) scaleY(1.1);
}

.header-container__burger:not(.open):hover span:nth-child(2) {
  transform: rotate(3deg) scaleY(1.1);
}

.header-container__burger:not(.open):hover span:nth-child(3) {
  transform: rotate(-4deg) scaleY(1.1);
}

@media (max-width: 646px) {
  .closed {
    display: block;
  }
}

@media (max-width: 646px) {
